@use "global" as *;

.p-facility {
  position: relative;
}

.p-facility__title {
  text-align: center;
}

.p-facility__sub-title {
  text-align: center;
  @include mq(md) {
    margin-top: myrem(-7);
  }
}

.p-facility__wrapper {
  margin: 0 auto;
  max-width: myrem(520);
  width: 100%;
  padding-right: myrem(15);
  padding-left: myrem(15);
  @include mq(md) {
    max-width: myrem(1400);
    padding-right: myrem(200);
    padding-left: myrem(200);
  }
}

.p-facility__cards {
  margin-top: myrem(32);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: myrem(26);
  column-gap: myrem(30);
  @include mq(md) {
    margin-top: myrem(36);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 3%;
    row-gap: myrem(31);
  }
}

.p-facility__card {
  margin: 0 auto;
  @include mq(md) {
    margin: 0;
    max-width: myrem(313);
  }
}

.p-facility__card-image img {
  border-radius: myrem(15);
  aspect-ratio: 344/204;
  @include mq(md) {
    border-radius: myrem(20);
    aspect-ratio: 313/186;
  }
}

.p-facility__card-body {
  padding-top: myrem(19);
  padding-bottom: myrem(11);
  @include mq(md) {
    padding-top: myrem(18);
    padding-bottom: myrem(6);
  }
}

.p-facility__card-title {
  font-size: myrem(21);
  letter-spacing: myrem(3);
  @include mq(md) {
    font-size: myrem(20);
  }
}

.p-facility__card-text {
  margin-top: myrem(9);
  padding-right: myrem(3);
  letter-spacing: myrem(0.4);
  @include mq(md) {
    margin-top: myrem(6);
    padding-right: 0;
  }
}
