@use "global" as *;

.p-header {
  width: 100%;
  height: myrem(60);
  position: fixed;
  top: 0;
  left: 0;
  background-color: $white;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include mq(md) {
    height: myrem(80);
    justify-content: unset;
    align-items: unset;
  }
}

.p-header__logo {
  padding-left: myrem(16);
  font-size: myrem(16);
  font-weight: 100;
  letter-spacing: myrem(1.6);
  display: flex;
  align-items: center;
  @include mq(md) {
    width: 31.3%;
    font-size: myrem(28);
    letter-spacing: myrem(2.8);
    line-height: 1.2;
    white-space: nowrap;
  }
}

.p-header__logo a {
  @include mq(md) {
    width: 100%;
    display: block;
    text-align: end;
  }
}

.p-header__information-box {
  padding-top: myrem(38);
  width: 100vw;
  height: calc(100vh - myrem(60));
  display: none;
  position: fixed;
  top: myrem(60);
  z-index: 100;
  background-color: $white;
  @include mq(md) {
    margin-left: auto;
    padding-top: 0;
    width: auto;
    height: 100%;
    display: flex;
    flex-grow: 1;
    position: unset;
  }
}

.p-header__information {
  @include mq(md) {
    margin-left: myrem(26);
    width: 40%;
    display: flex;
    justify-content: end;
    order: 2;
  }
}

.p-header__reservation {
  margin: 0 auto;
  padding-top: myrem(15);
  padding-bottom: myrem(18);
  max-width: myrem(315);
  width: 100%;
  order: 2;
  font-size: myrem(22);
  font-weight: 500;
  letter-spacing: myrem(2);
  color: $white;
  background-color: $blue2;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: myrem(10);
  @include mq(md) {
    margin: 0;
    padding: 0 3%;
    width: 37%;
    font-size: myrem(18);
    border-radius: 0;
  }
}

.p-header__reservation a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.p-header__reservation-inner {
  display: flex;
  align-items: center;
}

.p-header__reservation-image {
  margin-right: myrem(20);
  width: myrem(29);
  @include mq(md) {
    display: none;
  }
}

.p-header__tel {
  margin: myrem(11) auto 0;
  padding-bottom: myrem(9);
  max-width: myrem(315);
  width: 100%;
  font-size: myrem(26);
  font-weight: bold;
  color: $black2;
  text-align: center;
  background-color: rgb(164, 221, 224, 0.3);
  border-radius: myrem(10);
  @include mq(md) {
    margin: 0;
    padding: 0 3% 0 3%;
    width: 83%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 0;

    pointer-events: none;
    cursor: default;
    text-decoration: none;

    white-space: nowrap;
    text-align: center;
  }
}

.p-header__tel a {
  width: 100%;
  display: block;
}

.p-header__tel-image {
  width: myrem(16);
  display: inline-block;
  @include mq(md) {
    display: none;
  }
}

.p-header__tel-text {
  font-weight: bold;
  display: inline-block;
}

.p-header__tel-small-text {
  font-size: myrem(16);
  display: none;
  @include mq(md) {
    margin-right: myrem(-5);
    display: inline-block;
    letter-spacing: myrem(0.1);
  }
}

.p-header__tel-number {
  display: inline-block;
  @include mq(md) {
    font-size: myrem(26);
    letter-spacing: myrem(0.1);
  }
}

.p-header__opening-time {
  display: none;
  @include mq(md) {
    margin-top: myrem(-7);
    display: block;
    font-size: myrem(12);
    font-weight: 400;
  }
}

.p-header__comment {
  margin-top: myrem(-6);
  font-size: myrem(12);
  font-weight: 400;
  letter-spacing: myrem(0.4);
  color: $black;
  @include mq(md) {
    display: none;
  }
}

.p-header__nav {
  margin: 0 auto;
  padding-top: myrem(29);
  max-width: myrem(315);
  @include mq(md) {
    max-width: myrem(1000);
    margin: 0;
    padding-top: 0;
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
}

.p-header__nav-list {
  @include mq(md) {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: end;
    align-items: center;
  }
}

.p-header__nav-item {
  padding-left: myrem(9);
  padding-bottom: myrem(9);
  font-size: myrem(18);
  border-bottom: 1px solid $blue2;
  @include mq(md) {
    padding-left: 0;
    padding-bottom: 0;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    border-bottom: unset;
    white-space: nowrap;

    letter-spacing: normal;
  }
}

.p-header__nav-item::before {
  @include mq(md) {
    content: "";
    width: myrem(15);
    height: myrem(1);
    position: absolute;
    top: 50%;
    right: myrem(-34);
    transform: rotate(90deg);
    background-color: $black;
  }
}

.p-header__nav-item:not(:first-child) {
  margin-top: myrem(10);
  @include mq(md) {
    margin-top: 0;
    margin-left: myrem(50.6);
  }
}

.p-header__nav-item a {
  width: 100%;
  display: block;
  @include mq(md) {
    height: 100%;
    display: flex;
    align-items: center;
  }
}

.p-header__nav-item a:not(.p-header__sub-item a):hover {
  color: $blue1;
}

.p-header__nav-noborder {
  border-bottom: none;
}

.p-header__nav-arrow {
  position: relative;
}

.p-header__nav-arrow::before {
  content: "";
  display: inline-block;
  width: myrem(15);
  height: myrem(15);
  position: absolute;
  top: myrem(5);
  right: myrem(12);
  border-top: 2px solid $blue1;
  border-right: 2px solid $blue1;
  transform: rotate(135deg);
  @include mq(md) {
    display: none;
  }
}

.p-header__nav-arrow.open::before {
  top: myrem(13);
  transform: rotate(-45deg);
  @include mq(md) {
    display: none;
  }
}

.p-header__nav-arrow a {
  display: inline;
  @include mq(md) {
    display: flex;
  }
}

.p-header__sub-list {
  display: none;
  @include mq(md) {
    width: myrem(163);
    visibility: hidden;
    display: block;
    position: absolute;
    top: 100%;
    left: myrem(-24.5);
    text-align: center;
    background-color: rgb(255, 255, 255, 0.8);
    border-radius: 0 0 myrem(10) myrem(10);
    transition: all 0.5s;
    opacity: 0;
  }
}

.p-header__sub-item a {
  width: 100%;
  height: 100%;
  justify-content: center;
}

.p-header__nav-hover {
  cursor: pointer;
}

.p-header__nav-hover-colored:hover {
  transition: 0.3s;
  color: $blue1;
  opacity: 0.4;
}

.p-header__nav-hover:hover .p-header__sub-list {
  visibility: visible;
  opacity: 1;
}

.p-header__sub-item {
  margin-top: myrem(12);
  padding-top: myrem(8);
  padding-left: myrem(10);
  font-size: myrem(15);
  display: flex;
  align-items: center;
  @include mq(md) {
    margin-top: 0;
    padding: 0;
    height: myrem(59);
    letter-spacing: myrem(2);
  }
}

.p-header__sub-item:first-child {
  border-top: 1px solid $blue2;
  @include mq(md) {
    border-top: none;
  }
}

.p-header__sub-item::before {
  margin-right: myrem(9);
  content: "";
  width: myrem(16);
  height: myrem(1);
  background-color: $blue1;
  @include mq(md) {
    display: none;
  }
}

.p-header__sub-item:not(:last-child):after {
  @include mq(md) {
    content: "";
    width: myrem(133);
    height: myrem(1);
    display: inline-block;
    background-color: rgb(72, 72, 72, 0.3);
    position: absolute;
    left: myrem(12);
    margin-top: myrem(52);
  }
}

.p-header__sub-item:hover {
  color: $black;
}

/* ハンバーガーメニュー展開時、背景を固定 */
body.active {
  height: 100%;
  overflow: hidden;
}
